<template>
    <div class="swiperBox">
        <!-- 轮播图片 -->
        <div  :class="{swiperShow:true}">
            <swiper :options="swiperOption" ref="mySwiper" v-if="swiperImgs.length > 1 " @someSwiperEvent="callback">
                <!-- slides -->
                <swiper-slide v-for="(item, index) in swiperImgs" :key="index">
                    <div class="swiperItemBox">
                        <img  class="swiper-slide-img" :src="item.PIC_URL" v-if="index==0||index==(swiperImgs.length-1)" />
                        <img class="swiper-slide-img" :src="item.PIC_URL" :key="item.PIC_URL" alt="" srcset=""  v-else>
                    </div>
                </swiper-slide>
                <!-- Optional controls -->
                <template v-slot:pagination>
                    <div class="swiper-pagination"></div>
                </template>

            </swiper>
        </div>
        <!-- 一张图片 -->
        <div v-if="swiperImgs.length == 1" :class="{swiperOne:true}">
            <div v-for="(item,index) in swiperImgs" :key="index" @click="toSwiperDetail(index)">
                <div class="swiperItemBox">
                    <img class="swiper-slide-img" :src="item.PIC_URL" :key="item.PIC_URL" alt="" srcset="" >
                </div>
                
            </div>
        </div>
    </div>

</template>


<script>
import 'swiper/dist/css/swiper.css';
import { swiper, swiperSlide } from 'vue-awesome-swiper'
//bannerUrl跳转
import {skipLink} from "../../utils/skipLinkManage";
export default {
    components: {
        swiper,
        swiperSlide
    },
    props: {
        areaList:{
            type: Array,
            default:[],
        },
        swiperBox:{
            type:Object,
            default:function(){
                return{

                }
            }
        },
        // swiper图片样式
        swiperImgStyle:{
            type: Object,
            default: function () {
                return {
                    height:'auto',
                    width:'100%'
                }
            }
        },
        // 是否自动播放
        isAuto:{
            type:String,
            default:'0',
        },
        // 轮播图片数据
        swiperImgs: {
            type: Array,
            required: true,
            default: function () {
                return [];
            },
        },
        // 选择分页器
        paginationType: {
            type: String,
            default: '1'
        }
    },
    data: function () {
        return {
            that:this,
            // swiper 配置
            swiperOption: {
                // 自动切换
               autoplay:{
                    enabled: true,
                    // 用户操作后重新启动自动轮播
                    disableOnInteraction: false,
                    // 自动播放时间
                    delay: 3000
                },
                //首个居中
                centeredSlides: true,
                // 自动高度，让 siwper 的高度跟随 slide 的高度变化
                autoHeight: true,
                // 启动动态检查器
                observer: true,
                // 启动父类动态检查器
                observeParents: true,
                // 无限循环模式
                loop:true,
                on: {
                   click:()=>{
                       let swiper = this.$refs.mySwiper.swiper;
                       this.toSwiperDetail(swiper.realIndex);
                   }
                },
                // 设置分页器效果
                pagination: {
                    // 分页器对应的 html 元素
                    el: '.swiper-pagination',
                    // 分页器的样式
                    type: 'bullets',
                    // 点击跳转
                    clickable :true,
                    // 分页器内元素的类名
                    bulletClass: 'custom-bullet-class'
                },

            },

        }
    },
    watch: {
        swiperImgs: function(swiperImgs) {
            this.resetUI();
        }
    },
    methods: {
        resetUI: function() {
            if (this.swiperImgs == undefined 
            || this.swiperImgs.length == 0) {
                return;
            }

            this.initPaginationLayout();
        },
        //    配置不同的swiper
        initPaginationLayout: function () {
             /**
            * 根据分页器类型配置对应的分页器
            */
            
            
            
            // // 判断是否自动播放
            // let autoPlay = this.isAuto == '1' ? true : false;
            // this.swiperOption.autoplay = {
            //     enabled: autoPlay,
            //     // 用户操作后重新启动自动轮播
            //     disableOnInteraction: autoPlay,
            //     // 自动播放时间
            //     delay: 3000
            // };
        },
        // 跳转到不同的swiper详情
        toSwiperDetail:function(index){
            console.log(this.areaList);
            if (index < this.swiperImgs.length) {
                let item = this.swiperImgs[index];
                skipLink(this, this.areaList.length,item)
            }
        },
        callback(){

        }
    }
}
</script>


<style>
.swiperBox{
    width: 100%;
    padding-top: 15px ;
}
.swiperOne{
    width: 100%;
    text-align: center;
}

.swiperShow{
    width: 100%;
}
.swiper-container {
    width: 100%;
}
.swiper-wrapper{
    width: 100%;
}
.swiper-slide{
    width: 100%;
    display: flex;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.swiperItemBox{
    width: 92%;
    height: 180px;
    margin: 0 4%;
    overflow: hidden;
    text-align: center;
    border-radius: 4px;
}
.swiper-slide-img{
    width: 100%;
    border-radius: 4px;
    
}
.swiper-pagination{
    bottom: 6px!important;

}
.custom-bullet-class{
    box-sizing: border-box;
    height:6px;
    width: 6px;
    background: #ffffff;
    border-radius: 3px;
    margin: 0 2.5px;
    display: inline-block;
    opacity: .6;
    outline: none;
}
.swiper-pagination-bullet-active {
    width:6px;
    background: #F25766;
    opacity: 1;
}
.swiper-pagination-fraction{
    left: auto;
    right: 0;
    width: auto;
    font-size: 16px;
    background-color: rgba(0, 0, 0, .3);
    padding: 6px 18px ;
    border-top-left-radius: 16px;
    border-bottom-left-radius: 16px;
    bottom: 32px;
    color: white;
}
</style>
