import http from '../../http';
import {Toast} from "vant";

// 获取问卷列表
const getQuestionList = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('/hisense-systemServer/app/owner/questionMgr/getQuestionList.do',reqData,true, 'homeUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

