import {checkUserAnswer} from "../services/servers/question"

const skipLink = function (_this, areaListCount, x) {
  let forward_url = [];
  if (x.FORWARD_FLAG == '1') {
    if (x.FORWARD_URL) { //超链接url
      forward_url = x.FORWARD_URL.split(',');
    }
    if (1 == x.CONTENT_TYPE_FLAG) {//1：广告-富文本 2：超链接(flag：0-url或者应用程序)
      let advHtml;
      if (x.CONTENT) {
        advHtml = x.CONTENT;
      } else {
        advHtml = x.CONTENTS;
      }
      let data = {
        advHtml: advHtml,
        flag: 1//富文本
      }
      _this.$router.push({
        name: 'outHtml',
        params: {
          object: data
        }
      })
    } else {
      console.log("【banner跳转】forward_url = " + forward_url)
      let flag = forward_url[0].substring(5);
      console.log(flag)

      if (0 == flag) {

        var data = {
          advHtml: forward_url[1].substring(5) + "?usercode=" + _this.$userInfo.OWNER_CODE + "&roomcode=" + _this.$userInfo.ROOM_CODE,
          flag: 0//表示超链接
        }

        // if ($rootScope.bIsAndroid) {
        //     cordova.InAppBrowser.open(data.advHtml, '_blank', 'location=yes,toolbar=yes,toolbarcolor=#ffffff,hideurlbar=yes,title=yes,titletext='+x.TITLE+',titlecolor=#000000');
        // } else {
        cordova.InAppBrowser.open(data.advHtml, '_blank', 'location=no,toolbar=yes,toolbarposition=top,toolbarcolor=#ffffff,title=yes,titletext=' + x.TITLE + ',titlecolor=#000000');
        // }
      } else if (11 == flag) {
        if (_this.$userInfo.isGygFlag == 1) {
          _this.$router.push({
            name: 'login',
            params: {}
          })
        } else if (areaListCount == 0) {
          let obj = {};
          obj["flag"] = 2;//去绑房间1.逛一逛2.登录无绑定房间3.登录有绑定房间
          _this.$router.push({
            name: 'selectArea',
            params: {
              object: obj
            }
          })
        } else if (areaListCount == 1) {
          _this.$router.push({name: "propertyBillList"});
        } else if (areaListCount > 1) {
          _this.$router.push({name: "paymentRoomList"});
        }
      } else if (22 == flag) {//房产租售-租房信息
        _this.$router.push({name: "rentHouse"});
      } else if (23 == flag) {//售房信息
        _this.$router.push({name: "buyHouse"});
      } else if (24 == flag) {//物业活动 活动详情页
        let datailData = {
          ACTI_CODE: forward_url[1].substring(5)
        };
        _this.$router.push({path: '/propertyActivityDetail', query: {object: JSON.stringify(datailData)}})
      } else if (25 == flag) {//店铺
        // key.SELLER_NO = forward_url[1].substring(5);
        console.log(forward_url[1].substring(5))
        _this.$router.push({
          name: 'shopPage',
          params: {
            shopCode: forward_url[1].substring(5)
          }
        })
        // $rootScope.goToNext();$state.go('productList', { SELLER_NO: forward_url[1].substring(5) });
      } else if (30 == flag) {
        _this.$router.push({
          name: 'activityTemplate',
          params: {
            templateId: forward_url[1].substring(5)
          }
        })
      } else if (31 == flag) {
        //商品详情
        _this.$router.push({
          name: 'goodsDetail',
          params: {
            spuCode: forward_url[1].substring(5)
          }
        })
      } else if (33 == flag) {
        //店铺详情
        _this.$router.push({
          name: 'shopPage',
          params: {
            shopCode: forward_url[1].substring(5)
          }
        })
      } else if (34 == flag) {
        //文章页
        _this.$router.push({
          name: 'qualityDetail',
          params: {
            articleNo: forward_url[1].substring(5),
            articleImg: 'share'
          }
        })
      } else if (35 == flag) {
        //问卷调查详情页
        //数据格式 FORWARD_URL: "FLAG:35,CODE:2020042109311904158,PUBLISH_CODE:2020042010490677432"
        let paperCode = forward_url[1].substring(5);
        let publishCode = forward_url[2].substring(13);

        let param = {
          publishCode: publishCode,
          paperCode: paperCode,
          roomCode: _this.$userInfo.ROOM_CODE
        };
        //检查是否已作答
        checkUserAnswer(param).then(
          res => {
            if (res.resultCode === "0") {
              if (res.data.disableFlag) {
                //已作答，跳转到问卷详情页面
                if (res.data.typeFlag == 1) {
                  //问卷
                  _this.$router.push({
                    name: "questionnaireFinish",
                    query: {
                      paperCode: paperCode,
                      publishCode: publishCode,
                      roomCode: _this.$userInfo.ROOM_CODE,
                      userCode: ""
                    }
                  });
                } else {
                  //投票
                  if (res.data.allowFlag == 1) {
                    _this.$router.push({
                      name: "totalSurface",
                      query: {
                        publishCode: publishCode,
                        roomCode: _this.$userInfo.ROOM_CODE,
                        userCode: _this.$userInfo.OWNER_CODE
                      }
                    });
                  } else {
                    _this.$toast("您没有权限查看..");
                  }
                }
              } else {
                //未作答，跳转到问卷填写页面
                _this.$router.push({
                  name: "questionnaireNotFinish",
                  query: {
                    paperCode: paperCode,
                    publishCode: publishCode
                  }
                });
              }
            } else {
              console.log("检查问卷作答状态失败 " + res.msg)
            }
          }
        )

      } else if (99 == flag) {//跳转到大转盘
        _this.$router.push({
          name: 'pointActivity',
          query: {
            promotionId: forward_url[1].substring(5),
          }
        })
      }
    }

  } else {

  }
}

const goWXShareDetail = function (_this, data) {
  if (data.type == "product") {
    _this.$router.push({name: "goodsDetail", params: {spuCode: data.params.spuCode}})
  }
  if (data.type == "qualityLife") {
    //文章页
    _this.$router.push({name: 'qualityDetail', params: {articleNo: data.params.articleNo, articleImg: 'share'}});
  }
  if (data.type == "activityTemplate") {
    _this.$router.push({name: "activityTemplate", params: {templateId: data.params.templateId}})
  }
}
export {
  skipLink,
  goWXShareDetail
};
